.button {
  display: flex;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;

  width: 100%;
  height: 56px;
  background-color: #fff;
  border-radius: 40px;

  font-weight: 700;
  font-size: 16px;
  line-height: 27px;
  text-align: center;
  text-decoration: none;
  color: $colorDarkGrey;

  outline: none;
  cursor: pointer;

  transition: all 0.3s;

  &:hover {
    box-shadow: 1px 4px 16px rgba(232, 232, 240, 0.4);
  }

  &:active {
    box-shadow: 1px 4px 16px rgba(232, 232, 240, 0.64);
  }
}
